<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'
import { getRoles, getSchool, getClass } from "data/listBody";

const dataOption = {
  leftMenu: [
    { id:'1', name:'校区：', placeholder:'请输入校区', module:'schoolId', type:'select'},
    { id:'2', name:'班级：', placeholder:'请输入班级', module:'classId', type:'select'},
    { name:'用户姓名：', placeholder:'请输入用户姓名', module:'name'},    
    { name:'所属角色：', placeholder:'请选择所属角色', module:'roleId', type:'select'},    
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r2', name:'新增'}
  ]
}

const dataTable = {
  table: [
    { label:'校区', prop:'schoolName'},
    { label:'班级', prop:'className'},
    // { label:'带班老师', prop:'teacherName'},
    { label:'用户姓名', prop:'name'},
    { label:'用户手机号', prop:'mobile'},
    { label:'所属角色', prop:'roleName'},
  ],
  edit: [
    // {name: '邀请列表', id: 't3'},
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'systemUser',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.dropInit();
    this.search();
  },
  activated(){
    this.search();
  },
  methods:{
    dropInit(){ // 数据初始化
        getSchool(res => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
            if (item.module === "schoolId") item.options = res;
            return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
        getRoles((res) => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if(item.module === 'roleId') item.options = res;
                return item;
            })
            this.dataOption.leftMenu = leftMenu;
        },'','less')
    },
    optchange(params){
      console.log(params.id + '--id被单击了')
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.push(`/systemManage/userAdd?type=new`);
    },
    tabchange(params){
      console.log(params.id + '--id被单击了')
      this.id = params.data.id;
      // 编辑
      if(params.id === 't1') return this.$router.push(`/systemManage/userAdd?type=edit&id=${this.id}`);
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
      // 邀请列表
      if(params.id === 't3') return this.$router.push(`/systemManage/userInvite?type=detail`);
    },
    sure(){
      this.api.delete(`/user/${this.id}`).then((res)=>{
        this.loading = false;
        this.search();
        this.$message(res.data);     
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };
      const params = Object.assign({}, optParams, pageParams, {
          student: false
      });
      const param = queryString.stringify(params || {});

      this.loading = true;
      this.api.get(`/user?${param}`).then((res)=>{
        this.loading = false;
        this.dataInit(res.data);
        this.setPage(res.data);
        this.tableList = res.data.list;
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(data){//数据初始化
    }
  }
}
</script>
 
<style lang="less" scoped>

</style>
 