<template>
  <el-container>
    <el-main v-loading="loading">
      <el-table :data="tableList">
        <el-table-column v-for="(item, index) in data.table" :prop="item.prop" :label="item.label" :width="item.width" align="center" :key="index" />
        <el-table-column v-if="data.edit && data.edit.length > 0" label="操作" align="center">
          <template slot-scope="scope">
            <el-button v-for="(item, index) in data.edit" @click="handle(scope.row, item)" type="text" size="small" :key="index">{{item.name}}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "table",
  props:['data', 'list', 'loading'],
  data() {
    return {
      tableList: [],
    };
  },
  mounted(){
  },
  methods: {
    handle(scope, item){
      const params = {
        data: scope,
        id: item.id
      }
      this.$emit('change', params);
    }
  },
  watch:{
    list(newV){
      this.tableList = newV;
    }
  } 
};
</script>
<style lang="less" scoped>
</style>