const pageParams = {
    size: 20,
    totalCount: 1,
    totalPage: 1
}
const page = {
  data() {
    return {
      pageParams,
      currentPage: 1
    }
  },
  methods: {
    setPage(val) {
      this.pageParams.totalCount = val.totalCount
      this.pageParams.totalPage = val.totalPage
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val
    }
  },
  watch: {
      currentPage() {
        this.search()
      }
    }
}
export default page